"use client";

import { Eye, EyeOff, InfoIcon, LoaderCircle } from "lucide-react";
import { useRouter } from "next/navigation";
import React, { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Alert, AlertDescription } from "@/components/ui/alert";

import Link from "next/link";

import { motion } from "framer-motion";

const SignUpComp = () => {
  const [emailAddress, setEmailAddress] = useState("");
  const [password, setPassword] = useState("");
  const [showPassword, setShowPassword] = useState(false);
  const [loading, setLoading] = useState(false);

  const [error, setError] = useState(false);
  const router = useRouter();

  async function submit(e: React.FormEvent) {
    e.preventDefault();
    setLoading(true);
    const res = await fetch("/api/auth/signup", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ emailAddress, password }),
    });
    setLoading(false);
    if (res.ok) router.push("/main");
    else {
      const j = await res.json();
      console.log(j?.error || "Error");
      setError(true);
    }
  }

  return (
    <div className="h-[100dvh] flex flex-col md:flex-row ">
      <section className="flex-1 flex items-center justify-center p-8">
        <form onSubmit={submit} className="w-full max-w-md space-y-6">
          <motion.div
            initial={{ clipPath: "inset(0% 0% 100% 0%)" }}
            animate={{ clipPath: "inset(0% 0% 0% 0%)" }}
            transition={{ type: "spring", damping: 25, stiffness: 40 }}
            className="space-y-6"
          >
            <div className="space-y-2 pb-6 text-center">
              <h1 className="text-4xl md:text-5xl font-semibold tracking-tight">
                Create account
              </h1>

            </div>

            <div className="grid gap-4">
              <div className="grid gap-2">
                <Label htmlFor="email">Email</Label>
                <Input
                  onChange={(e) => setEmailAddress(e.target.value)}
                  value={emailAddress}
                  id="email"
                  type="email"
                  placeholder="you@example.com"
                  required
                />
              </div>
              <div className="grid gap-2">
                <Label htmlFor="password">Password</Label>
                <div className="relative">
                  <Input
                    onChange={(e) => setPassword(e.target.value)}
                    value={password}
                    id="password"
                    type={showPassword ? "text" : "password"}
                    required
                  />
                  <button
                    type="button"
                    onClick={() => setShowPassword(!showPassword)}
                    className="absolute inset-y-0 right-3 flex items-center"
                    aria-label={
                      showPassword ? "Hide password" : "Show password"
                    }
                  >
                    {showPassword ? (
                      <EyeOff className="w-5 h-5 text-muted-foreground" />
                    ) : (
                      <Eye className="w-5 h-5 text-muted-foreground" />
                    )}
                  </button>
                </div>

                <div className="flex items-center gap-1">
                  <InfoIcon className="w-3 h-3 " />
                  <p className="text-xs ">
                    Password must be at least 8 characters long!
                  </p>
                </div>
              </div>

              <Button
                type="submit"
                className="w-full cursor-pointer  mt-2"
                disabled={loading}
              >
                {loading ? (
                  <div className="flex gap-2 items-center">
                    <LoaderCircle className="h-8 w-8 animate-spin" />
                    <p>Creating...</p>
                  </div>
                ) : (
                  "Create account"
                )}
              </Button>

              {/* {error && (
                  <Alert variant="destructive">
                    <AlertDescription>{error}</AlertDescription>
                  </Alert>
                )} */}
            </div>
            <div className="text-center text-sm">
              Already have an account?{" "}
              <Link
                href="/auth/sign-in"
                className="text-pink-500 hover:underline"
              >
                Sign in
              </Link>
            </div>
          </motion.div>
        </form>
      </section>
    </div>
  );
};

export default SignUpComp;
